import React, { Component } from "react";
import { Row, Col, Divider } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTable from "../../../Tables/antTables/PrintTable.style";
import PrintTransaction, {
  PrintField,
  FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import { TotalField, Signature } from "../../../App/app.style";
import { convertDiscountMask, getFileName } from "../../../../helpers/helper";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import { siteConfig } from "../../../../settings";

class Print extends Component {
  render() {
    const data = this.props.data ? this.props.data : [];
    const { loading } = this.props;

    const columnPurchaseInvoice = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        align: "center"
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code"
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        render: text => {
          if (text && text.length > 25) {
            return text.substring(0, 25) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "PIQtys" }),
        dataIndex: "qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.return_unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "piBaseQty" }),
        dataIndex: "pi_base_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat
              value={parseFloat(row.qty) * parseFloat(row.unit_conv)}
            />{" "}
            {row.base_unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "price" }),
        dataIndex: "cost_per_unit_curr",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "rtrndQty" }),
        dataIndex: "returned_qty",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "returnQty" }),
        dataIndex: "return_qty",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "returnUnit" }),
        dataIndex: "return_unit_code"
      },
      {
        title: getLanguage({ id: "returnSubTotal" }),
        dataIndex: "return_sub_total",
        align: "right",
        render: text => <NumberFormat value={text} />
      }
    ];

    const columnPurchaseReceipt = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        align: "center"
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code",
        render: text => {
          if (text && text.length > 10) {
            return text.substring(0, 10) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        render: text => {
          if (text && text.length > 25) {
            return text.substring(0, 25) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "PRQtys" }),
        dataIndex: "qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.return_unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "prBaseQty" }),
        dataIndex: "pr_baseqty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat
              value={parseFloat(row.qty) * parseFloat(row.unit_conv)}
            />{" "}
            {row.base_unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "price" }),
        dataIndex: "cost_per_unit_curr",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "rtrndQty" }),
        dataIndex: "returned_qty",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "returnQty" }),
        dataIndex: "return_qty",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "returnUnit" }),
        dataIndex: "return_unit_code"
      },
      {
        title: getLanguage({ id: "returnSubTotal" }),
        dataIndex: "return_sub_total",
        align: "right",
        render: text => <NumberFormat value={text} />
      }
    ];

    const leftSide = [
      {
        label: <IntlMessages id="purchaseInvoices.currency" />,
        description: data.currency ? data.currency.currency_code : null
      },
      {
        label: <IntlMessages id="currencyFiscalRate" />,
        description: (
          <>
            <NumberFormat value={data.currency_rate} />/
            <NumberFormat value={data.fiscal_rate} />
          </>
        )
      },
      {
        label: <IntlMessages id="purchaseReturns.returnReason" />,
        description: data.return_reason
          ? data.return_reason.description
            ? data.return_reason.description
            : null
          : null
      }
    ];

    const rightSide = [
      {
        label:
          data.transaction_type === 1 ? (
            <IntlMessages id="purchaseReceipts.purchaseReceiptNo" />
          ) : (
            <IntlMessages id="purchaseInvoices.purchaseInvoiceNo" />
          ),
        description: data.transaction_no
      },
      {
        label: <IntlMessages id="purchaseReturns.location" />,
        description: data.location ? data.location.location_name : null
      }
    ];

    const total =
      data.transaction_type === 1
        ? [
            {
              label: (
                <>
                  <IntlMessages id="purchaseReturns.totalReturnQuantity" />
                  <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
                </>
              ),
              amount: <NumberFormat value={data.total_qty} />
              // parseAmount: true
            },
            {
              label: <IntlMessages id="total" />,
              amount: <NumberFormat value={data.total_amount} />
              // parseAmount: true
            }
          ]
        : [
            {
              label: (
                <>
                  <IntlMessages id="purchaseReturns.totalReturnQuantity" />
                  <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
                </>
              ),
              amount: <NumberFormat value={data.total_qty} />
              // parseAmount: true
            },
            {
              // isDiscount: true,
              label: <IntlMessages id="purchaseReturns.totalDiscount" />,
              // discount: convertDiscountMask(data.discount),
              amount: <NumberFormat value={data.total_discount} />
              // parseAmount: true
            },
            {
              label: <IntlMessages id="purchaseReturns.totalTax" />,
              amount: <NumberFormat value={data.total_tax} />
              // parseAmount: true
            },
            {
              label: <IntlMessages id="total" />,
              amount: <NumberFormat value={data.total_amount} />
              // parseAmount: true
            }
          ];

    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      }
    ];
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="sidebar.purchaseReturns" />}
          subtitle={
            <>
              <Row>
                <Col>
                  <strong>{data.purchase_return_no}</strong>
                </Col>
              </Row>
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data.return_date
                        ? moment(
                            data.return_date,
                            "YYYY-MM-DD HH:mm:ss"
                          ).format("DD MMMM YYYY, HH:mm:ss")
                        : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >
          {/* Bagian atas (Subheader) */}
          <Row>
            <Col span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={8}>
                  <IntlMessages id="sidebar.supplier" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={13}>
                  <strong>
                    {data.supplier ? data.supplier.supplier_name : null}
                  </strong>
                </Col>
              </Row>
            </Col>

            <Col span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={8}>
                  <IntlMessages id="note" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={15}>
                  <span>{data.note}</span>
                </Col>
              </Row>
            </Col>
          </Row>

          {/* Bagian list detail */}
          <Row style={{ marginTop: "24px" }}>
            <FieldCol leftSide={leftSide} rightSide={rightSide} />
          </Row>

          {/* Bagian Table */}
          <Row style={{ marginTop: "24px" }}>
            <Divider
              orientation="left"
              style={{
                fontSize: "12px",
                alignItems: "center",
                display: "flex"
              }}
            >
              <IntlMessages
                id="purchaseInvoices.tabs.item"
                style={{ marginTop: "-10" }}
              />
            </Divider>
            <Col span={24}>
              <PrintTable
                columns={
                  data.transaction_type === 1
                    ? columnPurchaseReceipt
                    : columnPurchaseInvoice
                }
                dataSource={data.purchase_return_details}
                rowKey={row => row.id_purchase_return_detail}
                pagination={false}
              />
            </Col>
          </Row>

          <Row type="flex" style={{ marginTop: "24px" }}>
            <Col push={12} span={12}>
              <TotalField data={total} />
            </Col>
          </Row>

          <Row style={{ marginTop: "24px" }}>
            <Col span={24}>
              <Signature data={signatureData} />
            </Col>
          </Row>
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint loading={loading} style={{ marginLeft: "5px" }} />
          )}
          content={() => this.componentRef}
          onBeforePrint={() =>
            (document.title = getFileName(
              "purchase_return",
              data.purchase_return_no,
              true
            ))
          }
          onAfterPrint={() => (document.title = siteConfig.documentTitle)}
        />
      </>
    );
  }
}

export default Print;
