import React, { Component } from "react";
import moment from "moment";

import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Form, Input, DatePicker, Select, Col, Row, Modal, Button } from "antd";

const { Option } = Select;
class ModalCash extends Component {
  handleSubmit = () => {
    // e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.due_date = values.due_date
          ? values.due_date.format("YYYY-MM-DD HH:mm:ss")
          : null;

        const data = Object.assign({}, values, { cash_return: 1 });
        this.props.submit(data);
      }
    });
  };

  handleReset = () => {
    const data = {
      bank_issuer: null,
      due_date: null,
      id_cash_bank: null,
      id_cash_flow_type: null,
      reference_no: null,
      cash_return: 0
    };
    this.props.submit(data);
  };

  render() {
    // console.log(this.props.transDate);
    const { getFieldDecorator } = this.props.form;
    const { transaction_status } = this.props;
    const { cashBanks, cashFlowTypes } = this.props ? this.props : [];
    const { purchaseReturn } = this.props.purchaseReturn
      ? this.props.purchaseReturn
      : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="purchaseReturns.setCash" />}
        visible={this.props.visible}
        centered
        closable={false}
        maskClosable={false}
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="50%"
        footer={
          <Row justify="end" type="flex">
            <Col>
              <Button
                onClick={() => {
                  this.props.handleModal();
                }}
              >
                <IntlMessages id="button.cancel" />
              </Button>

              <Button
                onClick={() => {
                  this.handleReset();
                }}
                disabled={transaction_status !== 1 ? true : false}
              >
                <IntlMessages id="filter.reset" />
              </Button>

              <Button
                type="primary"
                onClick={() => {
                  this.handleSubmit();
                }}
                disabled={transaction_status !== 1 ? true : false}
              >
                <IntlMessages id="button.ok" />
              </Button>
            </Col>
          </Row>
        }
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row type="flex" justify="start" gutter={6}>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="purchaseOrders.cashBank" />}
              >
                {getFieldDecorator("id_cash_bank", {
                  initialValue: purchaseReturn
                    ? purchaseReturn.id_cash_bank
                      ? purchaseReturn.id_cash_bank
                      : []
                    : [],
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    allowClear
                    showSearch
                    optionFilterProp="children"
                    placeholder={<IntlMessages id="purchaseOrders.cashBank" />}
                    disabled={transaction_status !== 1 ? true : false}
                  >
                    {cashBanks.map((data, key) => {
                      if (data.id_location === this.props.id_location) {
                        return (
                          <Option key={key} value={data.id_cash_bank}>
                            {data.bank_account_number === null
                              ? data.cash_bank_code
                              : data.cash_bank_code +
                                " (" +
                                data.bank_account_number +
                                ")"}
                          </Option>
                        );
                      }
                    })}
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="purchaseOrders.cashFlowType" />}
              >
                {getFieldDecorator("id_cash_flow_type", {
                  initialValue: purchaseReturn
                    ? purchaseReturn.id_cash_flow_type
                      ? purchaseReturn.id_cash_flow_type
                      : []
                    : [],
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    showSearch
                    allowClear
                    optionFilterProp="children"
                    placeholder={
                      <IntlMessages id="purchaseOrders.cashFlowType" />
                    }
                    disabled={transaction_status !== 1 ? true : false}
                  >
                    {cashFlowTypes.map((data, key) => (
                      <Option key={key} value={data.id_cash_flow_type}>
                        {`${data.cash_flow_type_code} (${data.description})`}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
          </Row>

          <Row type="flex" justify="start" gutter={6}>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="purchaseOrders.referenceNo" />}
              >
                {getFieldDecorator("reference_no", {
                  initialValue: purchaseReturn
                    ? purchaseReturn.reference_no
                      ? purchaseReturn.reference_no
                      : null
                    : null
                  // rules: [
                  //   {
                  //     required: true,
                  //     message: <IntlMessages id="required" />
                  //   }
                  // ]
                })(
                  <Input
                    disabled={transaction_status !== 1 ? true : false}
                    placeholder="Reference No."
                  />
                )}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="purchaseOrders.issuerDest" />}
              >
                {getFieldDecorator("bank_issuer", {
                  initialValue: purchaseReturn
                    ? purchaseReturn.bank_issuer
                      ? purchaseReturn.bank_issuer
                      : []
                    : [],
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Input
                    disabled={transaction_status !== 1 ? true : false}
                    placeholder="Issuer/Destination"
                  />
                )}
              </Form.Item>
            </Col>
          </Row>

          <Row type="flex" justify="start" gutter={6}>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="purchaseOrders.dueDate" />}
              >
                {getFieldDecorator("due_date", {
                  initialValue: purchaseReturn
                    ? purchaseReturn.due_date
                      ? moment(purchaseReturn.due_date)
                      : this.props.transDate
                    : this.props.transDate,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <DatePicker
                    style={{ width: "100%" }}
                    showTime
                    format="DD MMMM YYYY HH:mm:ss"
                    allowClear={true}
                    disabledDate={e => e.valueOf() <= this.props.transDate}
                    disabled={true}
                  />
                )}
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedCashModal = Form.create({ name: "pi_cash_modal" })(ModalCash);

export default WrappedCashModal;
