import React, { Component } from "react";
import { Form, InputNumber, Select } from "antd";
import EditableContext from "./editableContext";
import IntlMessages from "../../../../components/utility/intlMessages";
import NumberFormat from "../../../App/NumberFormat";

const FormItem = Form.Item;
const Option = Select.Option;

class EditableCell extends Component {
  state = {
    editing: false,
    err: null
  };

  toggleEdit = () => {
    const editing = !this.state.editing;

    this.setState({ editing }, () => {
      if (editing) {
        this.input.focus();
      }
    });
  };

  handleClickOutside = e => {
    const { editing } = this.state;

    if (editing && !this.cell) {this.toggleEdit();} else if (editing && this.cell !== e.target && !this.cell.contains(e.target)) {
      this.save();
      document.removeEventListener("click", this.handleClickOutside, true);
    }
  };

  save = (name, id) => {
    const { record, handleSave } = this.props;

    this.form.validateFields((error, values) => {
      const left_qty = (
        parseFloat(record.receive_qty) -
        parseFloat(record.returned_qty) -
        parseFloat(values.return_qty)
      ).toFixed(2);
      values.return_qty = parseFloat(values.return_qty);
      // values.return_sub_total =
      //   parseFloat(record.cost_per_unit) * parseFloat(values.return_qty);
      //   values.base_qty = (
      //     parseFloat(values.return_qty) * parseFloat(record.unit_conv)
      //   ).toFixed(2);

      if (parseFloat(left_qty) < 0) {
        this.setState({ err: <IntlMessages id="lessOrEqualPRQty" /> });
        return;
      }
      if (error) {
        return;
      }
      this.setState({ err: null });
      this.toggleEdit();

      if (name && name === "return_unit_code") {
        const [unit_conversion] = record.unit_conversion.filter(
          currData => currData.id_unit === id
        );

        const newRecord = Object.assign({}, record, {
          id_return_unit: unit_conversion ? unit_conversion.unit.id_unit : null,
          return_unit_code: unit_conversion
            ? unit_conversion.unit.unit_code
            : "-"
        });

        handleSave(newRecord);
      } else {
        handleSave({ ...record, ...values });
      }
    });
  };

  render() {
    const { editing, err } = this.state;
    const {
      editable,
      dataIndex,
      title,
      record,
      index,
      handleSave,
      status,
      colSpan,
      ...restProps
    } = this.props;
    return colSpan > 1 ? (
      <td colSpan={colSpan} style={{ textAlign: "right" }}>
        {restProps.children}
      </td>
    ) : colSpan === 0 ? null : (
      <td ref={node => (this.cell = node)} {...restProps}>
        {editable ? (
          <EditableContext.Consumer>
            {form => {
              this.form = form;
              if (editing && dataIndex === "return_unit_code") {
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      initialValue: record.id_return_unit
                    })(
                      <Select
                        ref={node => (this.input = node)}
                        showSearch
                        style={{ width: "100%" }}
                        onSelect={this.save.bind(this, "return_unit_code")}
                        optionFilterProp="children"
                        filterOption={true}
                      >
                        {record.unit_conversion.map((data, key) => {
                          if (record.pr_unit_conv) {
                            return parseFloat(data.unit_conversion) <=
                              record.pr_unit_conv ? (
                              <Option key={key} value={data.unit.id_unit}>
                                {data.unit.unit_code}
                              </Option>
                            ) : null;
                          } else {
                            return (
                              <Option key={key} value={data.unit.id_unit}>
                                {data.unit.unit_code}
                              </Option>
                            );
                          }
                        })}
                      </Select>
                    )}
                  </FormItem>
                );
              } else if (editing) {
                document.addEventListener(
                  "click",
                  this.handleClickOutside,
                  true
                );
                return (
                  <FormItem
                    style={{ margin: 0 }}
                    validateStatus={
                      err || form.getFieldError(dataIndex) ? "error" : null
                    }
                    help={err ? err : form.getFieldError(dataIndex)}
                  >
                    {form.getFieldDecorator(dataIndex, {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ],
                      initialValue: record[dataIndex]
                    })(
                      <InputNumber
                        ref={node => (this.input = node)}
                        onPressEnter={this.save}
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                        min={0.01}
                        precision={2}
                      />
                    )}
                  </FormItem>
                );
              } else if (
                !editing &&
                dataIndex === "return_qty" &&
                status === 1
              ) {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    <NumberFormat value={record[dataIndex]} />
                  </div>
                );
              } else if (
                !editing &&
                dataIndex === "return_qty" &&
                status !== 1
              ) {
                return (<NumberFormat value={record[dataIndex]} />);
              } else if (status === 1) {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {restProps.children}
                  </div>
                );
              } else if (status !== 1) {
                return restProps.children;
              }
            }}
          </EditableContext.Consumer>
        ) : dataIndex === "receive_qty" ? (
          record[dataIndex] + " " + record.unit_code
        ) : (
          restProps.children
        )}
      </td>
    );
  }
}

export default EditableCell;
