import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/purchase/purchaseReturn/actions";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Input,
  InputNumber,
  DatePicker,
  Select,
  Row,
  Col,
  Tooltip,
  Steps,
  Icon,
  Divider,
  Radio,
  Popconfirm,
  Empty
} from "antd";

import EditablePurchaseReceipt from "./editablePurchaseReceipt";
import EditablePurchaseInvoice from "./editablePurchaseInvoice";
import ModalPurchaseReceipt from "./modalPurchaseReceipt";
import ModalPurchaseInvoice from "./modalPurchaseInvoice";
import ModalCash from "./modalCash";
import ModalCancel from "./modalCancel";
// import { Object } from "core-js";
import { removeSaveData, getRate } from "../../../../helpers/helper";
import { customDot } from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;
const Step = Steps.Step;

const {
  getPurchaseInvoices,
  getPurchaseReceipts,
  setPrintData,
  confirmPurchaseReturn
} = actions;

class PurchaseReturnUpdateForm extends Component {
  state = {
    id_location: null,
    id_supplier: null,
    id_transaction: null,
    transaction_type: 1,
    transaction_no: null,
    item: null,
    items: [],
    ptDetails: {
      purchase_return_details: []
    },
    purchase_invoice: null,
    cashDetails: {
      id_cash_bank: null,
      id_cash_flow_type: null,
      reference_no: null,
      bank_issuer: null,
      due_date: null
    },
    discount: "0",
    currency_rate: 1,
    total_discount: 0,
    total_tax: 0,
    total_qty: 0,
    total_amount: 0,
    inclusive_tax: 0,
    visiblePurchaseReceipt: false,
    visiblePurchaseInvoice: false,
    visibleCash: false,
    visibleModalCancel: false,
    err: false,
    err_set_cash: null,
    restore: null,
    clear: false,
    cash_return: 0,
    can_set_cash: false,
    limit: 0
  };

  componentDidUpdate = (prevProps, prevState) => {
    const { purchaseReturn, loading } = this.props.purchaseReturn;
    const prevPurchaseReturn = prevProps.purchaseReturn.purchaseReturn;
    const { ptDetails } = this.state

    if (!loading && prevPurchaseReturn !== purchaseReturn) {
      if (purchaseReturn && purchaseReturn.purchase_invoice) {
        let sum = 0;
        purchaseReturn.purchase_invoice.purchase_invoice_details.map(
          item => sum += parseFloat(item.sub_total)
        );
        purchaseReturn.purchase_invoice.sum_sub_total = sum;
      }

      this.setState({
        id_transaction: purchaseReturn ? purchaseReturn.id_transaction : null,
        discount: purchaseReturn ? purchaseReturn.discount : "0",
        transaction_type: purchaseReturn ? purchaseReturn.transaction_type : 1,
        transaction_no: purchaseReturn ? purchaseReturn.transaction_no : null,
        currency_rate: purchaseReturn
          ? parseFloat(purchaseReturn.currency_rate)
          : 1,
        // cashDetails: {
        //   id_cash_bank: purchaseReturn ? purchaseReturn.id_cash_bank : null,
        //   id_cash_flow_type: purchaseReturn
        //     ? purchaseReturn.id_cash_flow_type
        //     : null,
        //   reference_no: purchaseReturn ? purchaseReturn.reference_no : null,
        //   bank_issuer: purchaseReturn ? purchaseReturn.bank_issuer : null,
        //   due_date: purchaseReturn ? purchaseReturn.due_date : null
        // },
        // cash_return: purchaseReturn ? purchaseReturn.cash_return : 0,
        // can_set_cash: purchaseReturn.cash_return ? true : false,
        inclusive_tax: purchaseReturn ? purchaseReturn.inclusive_tax : 0,
        items: purchaseReturn ? purchaseReturn.purchase_return_details : [],
        id_location: purchaseReturn ? purchaseReturn.id_location : null,
        id_supplier: purchaseReturn ? purchaseReturn.id_supplier : null,
        purchase_invoice: purchaseReturn.purchase_invoice
      });
    } else if (
      !loading &&
      prevState.ptDetails.purchase_return_details.length === 0 &&
      this.state.limit === 0 &&
      prevPurchaseReturn === purchaseReturn
    ) {
      if (purchaseReturn && purchaseReturn.purchase_invoice) {
        let sum = 0;
        purchaseReturn.purchase_invoice.purchase_invoice_details.map(
          item => sum += parseFloat(item.sub_total)
        );
        purchaseReturn.purchase_invoice.sum_sub_total = sum;
      }

      this.setState({
        id_transaction: purchaseReturn ? purchaseReturn.id_transaction : null,
        discount: purchaseReturn ? purchaseReturn.discount : "0",
        transaction_type: purchaseReturn ? purchaseReturn.transaction_type : 1,
        transaction_no: purchaseReturn ? purchaseReturn.transaction_no : null,
        currency_rate: purchaseReturn
          ? parseFloat(purchaseReturn.currency_rate)
          : 1,
        // cashDetails: {
        //   id_cash_bank: purchaseReturn ? purchaseReturn.id_cash_bank : null,
        //   id_cash_flow_type: purchaseReturn
        //     ? purchaseReturn.id_cash_flow_type
        //     : null,
        //   reference_no: purchaseReturn ? purchaseReturn.reference_no : null,
        //   bank_issuer: purchaseReturn ? purchaseReturn.bank_issuer : null,
        //   due_date: purchaseReturn ? purchaseReturn.due_date : null
        // },
        // cash_return: purchaseReturn ? purchaseReturn.cash_return : 0,
        // can_set_cash: purchaseReturn.cash_return ? true : false,
        inclusive_tax: purchaseReturn ? purchaseReturn.inclusive_tax : 0,
        items: purchaseReturn ? purchaseReturn.purchase_return_details : [],
        id_location: purchaseReturn ? purchaseReturn.id_location : null,
        id_supplier: purchaseReturn ? purchaseReturn.id_supplier : null,
        purchase_invoice: purchaseReturn.purchase_invoice,
        limit: 1
      });
    }

    if (prevState.ptDetails.purchase_return_details !== ptDetails.purchase_return_details) {
      const { setPrintData } = this.props;
      setPrintData(ptDetails);
    }

    /** Handle set cash ketika clear  */
    // if (this.state.clear) {
    //   this.setState({
    //     can_set_cash: false,
    //     cash_return: 0,
    //     cashDetails: {
    //       id_cash_bank: null,
    //       id_cash_flow_type: null,
    //       reference_no: null,
    //       bank_issuer: null,
    //       due_date: null
    //     },
    //     err_set_cash: null,
    //     err: false
    //   });
    // }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      const {
        id_transaction,
        ptDetails,
        discount,
        total_discount,
        total_tax,
        total_qty,
        total_amount,
        cashDetails,
        transaction_type,
        transaction_no,
        cash_return
        // can_set_cash
      } = this.state;

      if (err) {
        this.setState({ err: true });
        return;
      }
      // else if (can_set_cash && !cash_return) {
      //   this.setState({
      //     err: true,
      //     err_set_cash: <IntlMessages id="required" />
      //   });
      //   return;
      // }

      values.return_date = values.return_date.format("YYYY-MM-DD HH:mm:ss");

      const data = Object.assign({}, values, ptDetails, cashDetails, {
        id_transaction: id_transaction,
        transaction_type: transaction_type,
        transaction_no: transaction_no,
        cash_return: cash_return,
        discount: discount,
        total_discount: total_discount,
        total_tax: total_tax,
        total_qty: total_qty,
        total_amount: total_amount
      });
      // console.log(data);
      if (!err) {
        this.setState({
          err: false
          // err_set_cash: null
        });
        this.props.submit(data);
      }
    });
  };

  handleChangeTransDate = status => {
    if (!status) {
      const { getFieldsValue } = this.props.form;

      // const { currencies } = this.props.purchaseReturn;

      // let currRate = 1;
      // let fiscRate = 1;
      // if (getFieldValue("id_currency")) {
      //   const currency = currencies.find(
      //     data => data.id_currency === getFieldValue("id_currency")
      //   );
      //   const rate = getRate(
      //     currency.currency_rate,
      //     getFieldValue("return_date")
      //   );
      //   currRate = rate.currency_rate;
      //   fiscRate = rate.fiscal_rate;
      // }

      // setFieldsValue({
      //   currency_rate: currRate ? currRate : 1,
      //   fiscal_rate: fiscRate ? fiscRate : 1
      // });

      const { getPurchaseInvoices, getPurchaseReceipts } = this.props;
      
      let { return_date, id_location, id_supplier } = getFieldsValue([
        "return_date","id_location", "id_supplier"
      ]);
      return_date = 
        moment(return_date.format("YYYY-MM-DD HH:mm:ss")).endOf("day").format("YYYY-MM-DD HH:mm:ss");

      if(id_location && id_supplier) {
        const filterReceipt = 
          `id_supplier=${id_supplier}&id_receipt_location=${id_location}&transaction_date=${return_date}`;
        getPurchaseReceipts(1, null, filterReceipt);

        const filterInvoice = 
          `id_supplier=${id_supplier}&id_location=${id_location}&transaction_date=${return_date}`;
        getPurchaseInvoices(1, null, filterInvoice);
      }

      // this.setState({ currency_rate: currRate });
    }
  };

  handleModalCash = () => {
    const { visibleCash } = this.state;
    visibleCash
      ? this.setState({ visibleCash: false })
      : this.setState({ visibleCash: true });
  };

  handleModalPurchaseReceipt = () => {
    const { visiblePurchaseReceipt } = this.state;
    visiblePurchaseReceipt
      ? this.setState({ visiblePurchaseReceipt: false })
      : this.setState({ visiblePurchaseReceipt: true });
  };

  handleModalPurchaseInvoice = () => {
    const { visiblePurchaseInvoice } = this.state;
    visiblePurchaseInvoice
      ? this.setState({ visiblePurchaseInvoice: false })
      : this.setState({ visiblePurchaseInvoice: true });
  };

  handleSelectLocation = id_location => {
    const { setFieldsValue } = this.props.form;
    this.setState({ id_location: id_location, id_supplier: null, clear: true });
    setFieldsValue({ id_supplier: null });
  };

  handleSubmitCash = value => {
    this.setState({
      cashDetails: {
        id_cash_bank: value.id_cash_bank,
        id_cash_flow_type: value.id_cash_flow_type,
        reference_no: value.reference_no,
        bank_issuer: value.bank_issuer,
        due_date: value.due_date
      },
      cash_return: value.cash_return
    });
    this.handleModalCash();
  };

  handleSelectModalPurchaseReceipt = value => {
    const { purchaseReceipts } = this.props.purchaseReturn;
    const { setFieldsValue } = this.props.form;

    let purchaseReceipt = purchaseReceipts.find(
      curr_data => curr_data.id_purchase_receipt === value
    );

    let items = [...purchaseReceipt.purchase_receipt_details];
    items.map((data, index) => (data.index_no = index + 1));

    this.setState({
      id_transaction: purchaseReceipt.id_purchase_receipt,
      items: items,
      currency_rate: parseFloat(purchaseReceipt.purchase_order.currency_rate),
      discount: "0",
      transaction_no: purchaseReceipt.purchase_receipt_no,
      clear: false
    });

    setFieldsValue({
      currency_rate: purchaseReceipt.purchase_order.currency_rate,
      fiscal_rate: purchaseReceipt.purchase_order.fiscal_rate,
    });

    this.handleModalPurchaseReceipt();
  };

  handleSelectModalPurchaseInvoice = value => {
    const { purchaseInvoices } = this.props.purchaseReturn;
    const { setFieldsValue } = this.props.form;

    let purchaseInvoice = purchaseInvoices.find(
      curr_data => curr_data.id_purchase_invoice === value
    );

    let sum = 0;
    purchaseInvoice.purchase_invoice_details.map(
      item => sum += parseFloat(item.sub_total)
    );
    purchaseInvoice.sum_sub_total = sum;

    let items = [...purchaseInvoice.purchase_invoice_details];
    items.map((data, index) => (data.index_no = index + 1));

    this.setState({
      id_transaction: purchaseInvoice.id_purchase_invoice,
      discount: purchaseInvoice.discount,
      items: items,
      inclusive_tax: purchaseInvoice.inclusive_tax,
      currency_rate: parseFloat(purchaseInvoice.currency_rate),
      // can_set_cash: purchaseInvoice.payment_status === 2,
      transaction_no: purchaseInvoice.purchase_invoice_no,
      purchase_invoice: purchaseInvoice,
      clear: false
    });

    setFieldsValue({
      currency_rate: purchaseInvoice.currency_rate,
      fiscal_rate: purchaseInvoice.fiscal_rate,
    });

    this.handleModalPurchaseInvoice();
  };

  handleChangeSupplier = id_supplier => {
    const { suppliers } = this.props.purchaseReturn;
    const { setFieldsValue } = this.props.form;
    const supplier = suppliers.find(
      data => data.id_supplier === id_supplier
    );
    // const currency = currencies.find(
    //   data => data.id_currency === supplier.id_currency
    // );

    // const rate = getRate(currency.currency_rate, getFieldValue("return_date"));
    // const currRate = rate.currency_rate;
    // const fiscRate = rate.fiscal_rate;

    this.setState({
      id_supplier: id_supplier,
      // currency_rate: currRate,
      clear: true
    });

    setFieldsValue({
      id_currency: supplier ? supplier.id_currency : null,
      // currency_rate: currRate ? currRate : 1,
      // fiscal_rate: fiscRate ? fiscRate : 1
    });
  };

  handleTransType = e => {
    this.setState({
      transaction_type: e.target.value,
      ptDetails: { purchase_return_details: [] },
      transaction_no: null,
      clear: true
    });
  };

  handleSaveItemList = (
    items,
    total_discount,
    total_tax,
    total_qty,
    total_amount
  ) => {
    this.setState({
      ptDetails: { purchase_return_details: items },
      total_discount: total_discount,
      total_tax: total_tax,
      total_qty: total_qty,
      total_amount: total_amount,
      clear: false
    });
  };

  clearItems = (items, total_discount, total_tax, total_qty, total_amount) => {
    this.setState({
      items: [],
      ptDetails: { purchase_return_details: items },
      total_discount: total_discount,
      total_tax: total_tax,
      total_qty: total_qty,
      total_amount: total_amount,
      clear: false
    });
  };

  handleModalCancel = () => {
    this.setState({
      visibleModalCancel: !this.state.visibleModalCancel
    });
  };

  render() {
    // console.log(this.state.ptDetails);
    const {
      visiblePurchaseReceipt,
      visiblePurchaseInvoice,
      visibleCash
      // err_set_cash
    } = this.state;
    const { getFieldDecorator, getFieldValue } = this.props.form;

    const { purchaseReturn } = this.props;
    const {
      locations,
      suppliers,
      returnReasons,
      cashBanks,
      cashFlowTypes,
      currencies
    } = purchaseReturn ? purchaseReturn : [];

    const { user } = this.props;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    const {
      loading,
      loadingSubmit,
      loadingItems,
      loadingPurchaseReceipts,
      loadingPurchaseInvoices,
      loadingForeign
    } = purchaseReturn ? purchaseReturn : [];
    const data = purchaseReturn.purchaseReturn
      ? purchaseReturn.purchaseReturn
      : [];

    // const used = data.is_used;
    // const error = purchaseReturn
    //   ? purchaseReturn.errorField
    //     ? purchaseReturn.errorField
    //     : []
    //   : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalPurchaseReceipt
          visible={visiblePurchaseReceipt}
          purchaseReturn={purchaseReturn}
          handleModal={this.handleModalPurchaseReceipt}
          handleSelectModal={this.handleSelectModalPurchaseReceipt}
          id_location={getFieldValue("id_location")}
          id_supplier={getFieldValue("id_supplier")}
          transaction_date={getFieldValue("return_date")
            ? moment(getFieldValue("return_date")).endOf("day").format("YYYY-MM-DD HH:mm:ss")
            : moment().endOf("day").format("YYYY-MM-DD HH:mm:ss")
          }
        />

        <ModalPurchaseInvoice
          visible={visiblePurchaseInvoice}
          purchaseReturn={purchaseReturn}
          handleModal={this.handleModalPurchaseInvoice}
          handleSelectModal={this.handleSelectModalPurchaseInvoice}
          id_location={getFieldValue("id_location")}
          id_supplier={getFieldValue("id_supplier")}
          transaction_date={getFieldValue("return_date")
            ? moment(getFieldValue("return_date")).endOf("day").format("YYYY-MM-DD HH:mm:ss")
            : moment().endOf("day").format("YYYY-MM-DD HH:mm:ss")
          }
        />

        <ModalCash
          visible={visibleCash}
          transaction_status={data.status}
          handleModal={this.handleModalCash}
          id_location={getFieldValue("id_location")}
          submit={this.handleSubmitCash}
          purchaseReturn={{ purchaseReturn: this.state.cashDetails }}
          cashBanks={cashBanks}
          cashFlowTypes={cashFlowTypes}
          transDate={getFieldValue("return_date")}
        />

        <ModalCancel
          visible={this.state.visibleModalCancel}
          handleModal={this.handleModalCancel}
          submit={this.props.handleSubmitCancel}
          loading={loadingSubmit}
        />

        <Row style={{ margin: "12px 0px" }}>
          <Col span={12}>
            <Steps
              size="small"
              progressDot={customDot}
              current={data.status - 1}
            >
              <Step title={getLanguage({ id: "pending" })} />
              <Step title={getLanguage({ id: "processed" })} />
              <Step title={getLanguage({ id: "cancelled" })} />
            </Steps>
          </Col>
        </Row>

        <Row>
          <Col span={12} style={{ paddingRight: "15px" }}>
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="purchaseReturns.purchaseReturnNo" />}
                >
                  {getFieldDecorator("purchase_return_no", {
                    initialValue: data.purchase_return_no
                  })(<Input disabled={true} />)}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="purchaseReturns.transactionDate" />}
                  style={{ marginBottom: 0, paddingBottom: 0 }}
                >
                  {getFieldDecorator("return_date", {
                    initialValue: data.return_date
                      ? moment(data.return_date, "YYYY-MM-DD HH:mm:ss")
                      : null
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY HH:mm:ss"
                      disabled={
                        !permissions.includes("Can Back Date") ||
                        data.status !== 1
                      }
                      allowClear={false}
                      disabledDate={e => e.valueOf() > moment()}
                      onOpenChange={this.handleChangeTransDate}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <Col span={24}>
              <InputGroup>
                <Col span={16}>
                  <Form.Item
                    {...formItemLayout}
                    label={
                      <Tooltip
                        placement="top"
                        title={
                          <IntlMessages id="purchaseReturns.location.help" />
                        }
                      >
                        <IntlMessages id="purchaseReturns.location" />
                        <Icon
                          style={{ marginLeft: "5px" }}
                          type="question-circle"
                          theme="filled"
                        />
                      </Tooltip>
                    }
                  >
                    {getFieldDecorator("id_location", {
                      initialValue: data.id_location,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        showSearch
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="purchaseReturns.location" />
                        }
                        loading={loadingForeign}
                        onSelect={this.handleSelectLocation}
                        optionFilterProp="children"
                        filterOption={true}
                        disabled={data.status !== 1}
                      >
                        {locations.map((data, key) => (
                          <Option key={key} value={data.id_location}>
                            {`${data.location_code} (${data.location_name})`}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                </Col>

                <Col span={8}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="purchaseReturns.returnFrom" />}
                  >
                    {getFieldDecorator("transaction_type", {
                      initialValue: data.transaction_type
                    })(
                      <Radio.Group
                        onChange={this.handleTransType}
                        // value={this.state.inclusive_tax}
                        buttonStyle="solid"
                        style={{ width: "100%" }}
                        disabled={data.status !== 1}
                      >
                        <Tooltip
                          title={
                            <IntlMessages id="purchaseReceipts.pageHeader" />
                          }
                          placement="bottom"
                        >
                          <Radio.Button
                            style={{ width: "50%", textAlign: "center" }}
                            value={1}
                          >
                            PR
                          </Radio.Button>
                        </Tooltip>
                        <Tooltip
                          title={
                            <IntlMessages id="purchaseInvoices.pageHeader" />
                          }
                          placement="bottom"
                        >
                          <Radio.Button
                            style={{ width: "50%", textAlign: "center" }}
                            value={2}
                          >
                            PI
                          </Radio.Button>
                        </Tooltip>
                      </Radio.Group>
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>
            </Col>
          </Col>
          <Col span={12} style={{ paddingLeft: "15px" }}>
            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="purchaseReturns.note" />}
            >
              {getFieldDecorator("note", {
                initialValue: data.note
              })(
                <TextArea
                  disabled={data.status !== 1}
                  autosize={{ minRows: 4 }}
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12} style={{ paddingRight: "15px" }}>
            <InputGroup>
              <Col span={16}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="purchaseReturns.supplier" />}
                >
                  {getFieldDecorator("id_supplier", {
                    initialValue: data.id_supplier,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      showSearch
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="purchaseReturns.supplier" />
                      }
                      loading={loadingForeign}
                      onSelect={this.handleChangeSupplier}
                      notFoundContent={
                        getFieldValue("id_location") ? (
                          <Empty description={<IntlMessages id="noData" />} />
                        ) : (
                          <Empty
                            description={<IntlMessages id="noLocation" />}
                          />
                        )
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      disabled={data.status !== 1}
                    >
                      {suppliers.map((data, key) =>
                        data.id_location === getFieldValue("id_location") ? (
                          <Option key={key} value={data.id_supplier}>
                            {`${data.supplier_code} (${data.supplier_name})`}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <Tooltip
                      placement="top"
                      title={
                        <IntlMessages id="purchaseReturns.importTransaction.help" />
                      }
                    >
                      <IntlMessages id="purchaseReturns.importTransaction" />
                      <Icon
                        style={{ marginLeft: "5px" }}
                        type="question-circle"
                        theme="filled"
                      />
                    </Tooltip>
                  }
                >
                  {this.state.transaction_type === 1 ? (
                    <Button
                      icon="select"
                      disabled={
                        loadingPurchaseReceipts ||
                        (!getFieldValue("id_location") ||
                          !getFieldValue("id_supplier"))
                      }
                      onClick={this.handleModalPurchaseReceipt}
                      style={{ height: "31px", width: "100%" }}
                      // shape="circle"
                      disabled={data.status !== 1}
                    >
                      <IntlMessages id="purchaseReturns.buttonImportPR" />
                    </Button>
                  ) : (
                    <Button
                      icon="select"
                      disabled={
                        loadingPurchaseInvoices ||
                        (!getFieldValue("id_location") ||
                          !getFieldValue("id_supplier"))
                      }
                      onClick={this.handleModalPurchaseInvoice}
                      style={{ height: "31px", width: "100%" }}
                      // shape="circle"
                      disabled={data.status !== 1}
                    >
                      <IntlMessages id="purchaseReturns.buttonImportPI" />
                    </Button>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
          </Col>
          <Col span={12} style={{ paddingLeft: "15px" }}>
            <InputGroup>
              {/* <Col span={8}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <Tooltip
                      placement="top"
                      title={<IntlMessages id="purchaseReturns.setCash.help" />}
                    >
                      <IntlMessages id="purchaseReturns.setCash">
                        {txt => (
                          <span
                            className={
                              this.state.can_set_cash
                                ? "ant-form-item-required"
                                : null
                            }
                            style={{
                              color: err_set_cash ? "#f5222d" : "inherit"
                            }}
                          >
                            {txt}
                          </span>
                        )}
                      </IntlMessages>
                      <Icon
                        style={{ marginLeft: "5px" }}
                        type="question-circle"
                        theme="filled"
                      />
                    </Tooltip>
                  }
                  validateStatus={err_set_cash ? "error" : null}
                  help={err_set_cash ? err_set_cash : null}
                >
                  <Button
                    type={this.state.cash_return ? "primary" : "default"}
                    icon="plus"
                    disabled={
                      loadingPurchaseInvoices ||
                      loadingPurchaseReceipts ||
                      // data.status !== 1 ||
                      !this.state.can_set_cash
                    }
                    onClick={this.handleModalCash}
                    style={{ height: "31px", width: "100%" }}
                    // shape="circle"
                  >
                    <IntlMessages id="purchaseReturns.setCash.button" />
                  </Button>
                </Form.Item>
              </Col> */}
              <Col span={24}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <Tooltip
                      placement="top"
                      title={
                        <IntlMessages id="purchaseReturns.returnReason.help" />
                      }
                    >
                      <IntlMessages id="purchaseReturns.returnReason" />
                      <Icon
                        style={{ marginLeft: "5px" }}
                        type="question-circle"
                        theme="filled"
                      />
                    </Tooltip>
                  }
                >
                  {getFieldDecorator("id_return_reason", {
                    initialValue: data.id_return_reason,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      showSearch
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="purchaseReturns.returnReason" />
                      }
                      loading={loadingForeign}
                      optionFilterProp="children"
                      filterOption={true}
                      disabled={data.status !== 1}
                    >
                      {returnReasons.map((data, key) => (
                        <Option key={key} value={data.id_return_reason}>
                          {`${data.return_reason_code} (${data.description})`}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
          </Col>
        </Row>

        <Row>
          <Col span={12} style={{ paddingRight: "15px" }}>
            <InputGroup>
              <Col span={12}>
                {getFieldValue("transaction_type") === 1 ? (
                  <Form.Item
                    {...formItemLayout}
                    label={
                      <IntlMessages id="purchaseReceipts.purchaseReceiptNo" />
                    }
                  >
                    <Input value={this.state.transaction_no} disabled={true} />
                  </Form.Item>
                ) : (
                  <Form.Item
                    {...formItemLayout}
                    label={
                      <IntlMessages id="purchaseInvoices.purchaseInvoiceNo" />
                    }
                  >
                    <Input value={this.state.transaction_no} disabled={true} />
                  </Form.Item>
                )}
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="payablePayments.currency" />}
                >
                  {getFieldDecorator("id_currency", {
                    initialValue: data.id_currency
                  })(
                    <Select
                      showSearch
                      style={{ width: "100%" }}
                      loading={loadingForeign}
                      disabled={true}
                      optionFilterProp="children"
                      filterOption={true}
                    >
                      {currencies.map((data, key) => (
                        <Option key={key} value={data.id_currency}>
                          {data.currency_code}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
          </Col>
          <Col span={12} style={{ paddingLeft: "15px" }}>
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <Tooltip
                      placement="top"
                      title={
                        <IntlMessages id="payablePayments.currencyRate.help" />
                      }
                    >
                      <IntlMessages id="payablePayments.currencyRate" />
                      <Icon
                        style={{ marginLeft: "5px" }}
                        type="question-circle"
                        theme="filled"
                      />
                    </Tooltip>
                  }
                >
                  {getFieldDecorator("currency_rate", {
                    initialValue: data.currency_rate
                  })(
                    <InputNumber
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      min={1.0}
                      precision={2}
                      style={{ width: "100%" }}
                      disabled={true}
                      // disabled={
                      //   getFieldValue("id_currency") && currencies.length > 0
                      //     ? currencies.find(
                      //         cur =>
                      //           cur.id_currency === getFieldValue("id_currency")
                      //       ).is_base === 1 || data.status !== 1
                      //       ? true
                      //       : false
                      //     : false
                      // }
                      // onBlur={() =>
                      //   this.setState({
                      //     currency_rate: getFieldValue("currency_rate")
                      //   })
                      // }
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <Tooltip
                      placement="top"
                      title={
                        <IntlMessages id="payablePayments.fiscalRate.help" />
                      }
                    >
                      <IntlMessages id="payablePayments.fiscalRate" />
                      <Icon
                        style={{ marginLeft: "5px" }}
                        type="question-circle"
                        theme="filled"
                      />
                    </Tooltip>
                  }
                >
                  {getFieldDecorator("fiscal_rate", {
                    initialValue: data.fiscal_rate
                  })(
                    <InputNumber
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      min={1.0}
                      precision={2}
                      style={{ width: "100%" }}
                      disabled={true}
                      // disabled={
                      //   getFieldValue("id_currency") && currencies.length > 0
                      //     ? currencies.find(
                      //         cur =>
                      //           cur.id_currency === getFieldValue("id_currency")
                      //       ).is_base === 1 || data.status !== 1
                      //       ? true
                      //       : false
                      //     : false
                      // }
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
          </Col>
        </Row>
        {/* </TabPane> */}
        {/* <TabPane
            tab={<IntlMessages id="purchaseReturns.tabs.payment" />}
            key={2}
          > */}
        {/* </TabPane>
        </Tabs> */}

        {this.state.transaction_type === 1 ? (
          <div>
            <Divider orientation="left" style={{ fontSize: 14 }}>
              <IntlMessages id="purchaseReturns.subtitle.PRitems" />
            </Divider>

            <Row style={{ marginBottom: "40px" }}>
              <Col span={24}>
                <EditablePurchaseReceipt
                  items={this.state.items}
                  currency_rate={this.state.currency_rate}
                  status={data.status}
                  handleSaveItemList={this.handleSaveItemList.bind(this)}
                  clearItems={this.clearItems}
                  loadingTransactions={loadingPurchaseReceipts}
                  loadingItems={loadingItems}
                  clear={this.state.clear}
                />
              </Col>
            </Row>
          </div>
        ) : (
          <div>
            <Divider orientation="left" style={{ fontSize: 14 }}>
              <IntlMessages id="purchaseReturns.subtitle.PIitems" />
            </Divider>

            <Row style={{ marginBottom: "40px" }}>
              <Col span={24}>
                <EditablePurchaseInvoice
                  items={this.state.items}
                  purchase_invoice={this.state.purchase_invoice}
                  currency_rate={this.state.currency_rate}
                  discount={this.state.discount}
                  inclusive_tax={this.state.inclusive_tax}
                  status={data.status}
                  handleSaveItemList={this.handleSaveItemList.bind(this)}
                  clearItems={this.clearItems}
                  loadingTransactions={loadingPurchaseInvoices}
                  loadingItems={loadingItems}
                  clear={this.state.clear}
                />
              </Col>
            </Row>
          </div>
        )}

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "24px" }}
            loading={loading || loadingSubmit}
            disabled={
              !permissions.includes("Update Purchase Return") ||
              data.status !== 1
            }
            onClick={() => removeSaveData("pt")}
          >
            <IntlMessages id="button.submit" />
          </Button>

          {/* Confirm */}
          <Popconfirm
            title={<IntlMessages id="confirmTransaction" />}
            onConfirm={() =>
              this.props.confirmPurchaseReturn(data.id_purchase_return)
            }
            okText={<IntlMessages id="yes" />}
            cancelText={<IntlMessages id="no" />}
          >
            <Button
              style={{ marginLeft: "5px", padding: "0px 15px" }}
              disabled={
                data.status !== 1 ||
                !permissions.includes("Confirm Purchase Return")
                  ? true
                  : false
              }
              loading={loading || loadingSubmit}
            >
              <IntlMessages id="button.confirmTransaction" />
            </Button>
          </Popconfirm>

          {/* Cancel */}
          <Button
            style={{ marginLeft: "5px", padding: "0px 15px" }}
            disabled={
              data.status !== 2 ||
              !permissions.includes("Cancel Purchase Return")
                ? true
                : false
            }
            loading={loading || loadingSubmit}
            onClick={this.handleModalCancel}
          >
            <IntlMessages id="button.cancelTransaction" />
          </Button>

          <br />
          {this.state.err ? (
            <span style={{ color: "#f5222d" }}>
              <IntlMessages id="form.error" />
            </span>
          ) : null}
        </Form.Item>
      </Form>
    );
  }
}

const WrappedPurchaseReturnUpdateForm = Form.create({
  name: "update_purchase_return"
})(PurchaseReturnUpdateForm);

const mapStateToProps = state => {
  return {
    user: state.auth.user,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    {
      getPurchaseInvoices,
      getPurchaseReceipts,
      setPrintData,
      confirmPurchaseReturn
    }
  )
)(WrappedPurchaseReturnUpdateForm);
