import React, { Component } from "react";

import { Button, Popconfirm, Tooltip, Form } from "antd";
import EditableCell from "./editableCellPr";
import EditableContext from "./editableContext";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper from "../../../App/app.style";
import { getUnitConversion } from "../../../../helpers/helper";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { items } = props;

    this.state = {
      dataSource: [...items],
      count: items.length + 1,
      total_return: 0,
      total_return_amount: 0,
      visible: false
    };
  }

  componentDidUpdate(prevProps) {
    const {
      loadingTransactions,
      items,
      currency_rate,
      handleSaveItemList,
      clearItems,
      clear
    } = this.props;

    if (!loadingTransactions && items.length !== 0) {
      let newData = [];
      let newTotRetQty = 0;
      let newTotRetAmount = 0;
      let newIndex = 0;

      items.map(item => {
        let init = {};

        init.index_no = item.index_no;
        init.id_transaction_detail = item.id_transaction_detail
          ? item.id_transaction_detail
          : item.id_purchase_receipt_detail;
        init.item_code = item.item_code;
        init.item_name = item.item_name;
        init.receive_qty = parseFloat(item.receive_qty);
        init.qty = item.qty ? parseFloat(item.qty) : parseFloat(item.receive_qty);
        init.item_price = item.item_price
          ? item.item_price
          : item.item_price_purchase_order;
        init.cost_per_unit = parseFloat(item.cost_per_unit);

        // cost per unit dalam bentuk currency
        init.cost_per_unit_curr = init.cost_per_unit / parseFloat(currency_rate);
        init.sub_total = item.sub_total
          ? parseFloat(item.sub_total)
          : parseFloat(item.purchase_sub_total);
        init.returned_qty = item.returned_qty
          ? parseFloat(item.returned_qty)
          : item.pr_detail
          ? parseFloat(item.pr_detail.returned_qty)
          : 0;
        init.return_qty = item.return_qty ? parseFloat(item.return_qty) : init.qty - init.returned_qty;
        init.unit_conversion = item.unit_conversion
          ? item.unit_conversion
          : item.item.unit_conversion;
        init.id_return_unit = item.id_return_unit
          ? item.id_return_unit
          : item.id_unit;
        init.return_unit_code = item.unit
          ? item.unit.unit_code
          : item.return_unit_code;
        init.id_base_unit = item.id_base_unit
          ? item.id_base_unit
          : item.base_unit.id_unit;
        init.base_unit_code = item.base_unit
          ? item.base_unit.unit_code
          : item.base_unit_code;

        init.unit_conv = getUnitConversion(
          init.id_return_unit,
          init.unit_conversion
        );

        // untuk filter unit conversion yang dibawah PR unit
        init.pr_unit_conv = init.unit_conv;

        init.base_qty = init.return_qty * init.unit_conv;
        init.return_sub_total =
          init.cost_per_unit_curr * init.base_qty;

        newTotRetQty += init.base_qty;
        newTotRetAmount += init.return_sub_total;
        newIndex = parseInt(init.index_no) + 1;
        newData.push(init);
      });
      this.setState({
        dataSource: [...newData],
        count: newIndex,
        total_return: newTotRetQty,
        total_return_amount: newTotRetAmount
      });
      clearItems(newData, 0, 0, newTotRetQty, newTotRetAmount);
    }
    // clear item jika location/supplier berubah
    else if (clear) {
      this.setState({ dataSource: [], count: 1 });
      handleSaveItemList([], 0, 0, 0, 0);
    }
    // perubahan currency rate
    else if (prevProps.currency_rate !== currency_rate) {
      let newData = this.state.dataSource;
      let newTotRetQty = 0;
      let newTotRetAmount = 0;
      // const sumSubtotal = getSumSubtotal(newData);

      newData.map(data => {
        data.cost_per_unit_curr =
          data.cost_per_unit / parseFloat(currency_rate);
        data.return_sub_total =
          parseFloat(data.cost_per_unit_curr) * parseFloat(data.base_qty);

        newTotRetQty += parseFloat(data.base_qty);
        newTotRetAmount += parseFloat(data.return_sub_total);
      });

      this.setState({
        dataSource: [...newData],
        total_return: newTotRetQty,
        total_return_amount: newTotRetAmount
      });

      handleSaveItemList(newData, 0, 0, newTotRetQty, newTotRetAmount);
    }
  }

  handleDelete = index_no => {
    const { handleSaveItemList } = this.props;
    let newTotRetQty = 0;
    let newTotRetAmount = 0;

    const dataSource = this.state.dataSource;

    let newData = dataSource.filter(currData => currData.index_no !== index_no);
    newData.map((currData, index) => {
      currData.index_no = index + 1;
      newTotRetQty += parseFloat(currData.base_qty);
      newTotRetAmount += parseFloat(currData.return_sub_total);
    });

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1,
      total_return: newTotRetQty,
      total_return_amount: newTotRetAmount
    });
    handleSaveItemList(newData, 0, 0, newTotRetQty, newTotRetAmount);
  };

  handleSave = row => {
    const { handleSaveItemList } = this.props;
    const newData = this.state.dataSource;
    let newTotRetQty = 0;
    let newTotRetAmount = 0;

    const index = newData.findIndex(
      currData => row.index_no === currData.index_no
    );

    // row.unit_conv = getUnitConversion(row.id_return_unit, row.unit_conversion);
    row.base_qty = parseFloat(row.return_qty) * parseFloat(row.unit_conv);
    row.return_sub_total =
      parseFloat(row.cost_per_unit_curr) * parseFloat(row.base_qty);

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    newData.map(currData => {
      newTotRetQty += parseFloat(currData.base_qty);
      newTotRetAmount += parseFloat(currData.return_sub_total);
    });

    // console.log(row);

    this.setState({
      dataSource: [...newData],
      total_return: newTotRetQty,
      total_return_amount: newTotRetAmount
    });
    handleSaveItemList(newData, 0, 0, newTotRetQty, newTotRetAmount);
  };

  render() {
    // console.log(this.props.purchaseOrder);

    const { dataSource } = this.state;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        width: "5%",
        align: "right",
        render: text => {
          if (isNaN(text)) {
            return {
              children: text,
              props: {
                colSpan: 7
              }
            };
          } else {
            return {
              children: text,
              props: {
                colSpan: 1
              }
            };
          }
        }
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code",
        width: "25%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          if (text && text.length > 10) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 10) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        width: "30%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="PRQty" />}>
            <a href="javascript:;"><IntlMessages id="PRQtys" /></a>
          </Tooltip>
        ),
        dataIndex: "qty", // dari PR
        width: "15%",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          return (<><NumberFormat value={text} /> {row.return_unit_code}</>);
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="purchaseReceiptBaseQuantity" />}>
            <a href="javascript:;"><IntlMessages id="prBaseQty" /></a>
          </Tooltip>
        ),
        dataIndex: "pr_base_qty",
        align: "right",
        width: "10%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return (<><NumberFormat value={text} /> {row.base_unit_code}</>);
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="costPerUnit" />}>
            <a href="javascript:;"><IntlMessages id="cost" /></a>
          </Tooltip>
        ),
        dataIndex: "cost_per_unit_curr",
        width: "15%",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return (<NumberFormat value={text} /> );
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="returnedQty" />}>
            <a href="javascript:;"><IntlMessages id="rtrndQty" /></a>
          </Tooltip>
        ),
        dataIndex: "returned_qty", // dari PR
        width: "15%",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return (<><NumberFormat value={text} /> {row.return_unit_code}</>);
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="returnQuantity" />}>
            <a href="javascript:;"><IntlMessages id="returnQty" /></a>
          </Tooltip>
        ),
        dataIndex: "return_qty", // dari PR
        width: "15%",
        align: "right",
        editable: true,
        onCell: record => {
          if (isNaN(record.index_no)) {
            return {
              record,
              title: (
                <Tooltip placement="top" title={<IntlMessages id="returnQuantity" />}>
                  <a href="javascript:;"><IntlMessages id="returnQty" /></a>
                </Tooltip>
              ),
              dataIndex: "return_qty", // dari PR
              // width: "10%",
              status: this.props.status
            };
          } else {
            return {
              record,
              title: (
                <Tooltip placement="top" title={<IntlMessages id="returnQuantity" />}>
                  <a href="javascript:;"><IntlMessages id="returnQty" /></a>
                </Tooltip>
              ),
              dataIndex: "return_qty", // dari PR
              width: "10%",
              editable: true,
              handleSave: this.handleSave,
              status: this.props.status
            };
          }
        }
      },
      {
        title: getLanguage({ id: "returnUnit" }),
        dataIndex: "return_unit_code",
        align: "center",
        width: "10%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 4
              }
            };
          }

          return text;
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="returnBaseQuantity" />}>
            <a href="javascript:;"><IntlMessages id="returnBaseQty" /></a>
          </Tooltip>
        ),
        dataIndex: "base_qty",
        width: "15%",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return (<><NumberFormat value={text} /> {row.base_unit_code}</>);
        }
      },
      {
        title: getLanguage({ id: "returnSubTotal" }),
        dataIndex: "return_sub_total",
        width: "15%",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return (<NumberFormat value={text} /> );
        }
      },
    ];

    let operation = 
      {
        title: "",
        dataIndex: "operation",
        width: "15%",
        align: "center",
        fixed: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          else {
            return (
              <Popconfirm
                title={<IntlMessages id="confirmDelete" />}
                onConfirm={() => this.handleDelete(row.index_no)}
                okText={<IntlMessages id="yes" />}
                cancelText={<IntlMessages id="no" />}
              >
                <Tooltip
                  title={<IntlMessages id="delete" />}
                >
                  <Button
                    //style={{ borderRadius: "50%" }}
                    className="invoiceDltBtn"
                    size="small"
                  >
                    <i className="ion-trash-b" />
                  </Button>
                </Tooltip>
              </Popconfirm>
            );
          }
        }
      }

    if (!this.props.status || this.props.status === 1 && columns.length === 11){
      columns.push(operation)
    }

    // const total = getTotal(dataSource);
    const info = [
      {
        index_no: `${getLanguage({ id: "purchaseReturns.totalReturnQuantity" })} (${getLanguage({ id: "bsUnt" })})`,
        return_qty: <NumberFormat value={this.state.total_return} />
      },
      {
        index_no: getLanguage({ id: "total" }),
        return_qty: <NumberFormat value={this.state.total_return_amount} />
      }
    ];
    const modifiedData = dataSource.length > 0 ? [...dataSource, ...info] : [];

    return (
      <CardWrapper>
        <Scrollbars style={{ width: "100%" }}>
          <TableWrapper
            className="invoiceListTable"
            components={components}
            rowKey={record => record.index_no}
            rowClassName={() => "editable-row"}
            dataSource={modifiedData}
            columns={columns}
            pagination={false}
            // scroll={{ x: 1300 }}
          />
        </Scrollbars>
      </CardWrapper>

    );
  }
}

export default EditableTable;
