const getReturnDiscount = item => {
  let discount = item.discount;

  if (discount) {
    if (discount.slice(-1) === "%") {
      return discount;
    } else {
      return (
        (item.base_qty / item.pi_base_qty) *
        parseFloat(discount)
      ).toString();
    }
  } else {
    return "0";
  }
};

const getReturnDiscountAmount = item => {
  let discount = getReturnDiscount(item);
  if (discount.slice(-1) === "%") {
    return getDiscount(discount, item.item_price * item.return_qty);
  } else {
    return parseFloat(discount);
  }
};

const getSumSubTotal = items => {
  let sum_sub_total = 0;
  items.map(currData => {
    sum_sub_total += parseFloat(currData.sub_total);
  });
  sum_sub_total = parseFloat(sum_sub_total);
  return parseFloat(sum_sub_total);
};

/**
 * @param {array} items list item yang ada
 * @param {string} suppDiscount diskon dari supplier
 * @param {boolean} inclusiveTax inclusive tax atau tidak
 */
export const getTotalTax = (items, suppDiscount, inclusiveTax, sum_sub_total) => {
  let totalTax = 0;
  if (inclusiveTax) {
    items.map(data => {
      totalTax += getSubTotalTaxPerItemInclusive(
        data,
        items,
        suppDiscount,
        sum_sub_total
      );
    });
  } else {
    items.map(data => {
      totalTax += getSubTotalTaxPerItem(
        data,
        items,
        suppDiscount,
        sum_sub_total
      );
    });
  }

  totalTax = totalTax;
  return parseFloat(totalTax);
};

/**
 * @param {object} item item yang akan dihitung subtotal ppn & diskonnya
 * @param {array} items list items
 * @param {string} suppDiscount discount supplier
 * */
const getSubTotalTaxPerItem = (item, items, suppDiscount, sum_sub_total) => {
  const tax_pct = parseFloat(item.tax_pct);
  let subTotalTaxPerItem = "0.00";
  let subtotalPerItem = 0;
  let discountBillPerItem = 0;
  let discount_item = getReturnDiscount(item);
  // const price =
  //   (parseFloat(item.item_price) / parseFloat(item.pi_unit_conv)) *
  //   parseFloat(item.unit_conv) *
  //   item.return_qty;
  if (tax_pct === 0) {
    return parseFloat(subTotalTaxPerItem);
  } else if (tax_pct > 0) {
    subtotalPerItem = getSubTotalPerItem(
      item.item_price,
      item.return_qty,
      discount_item
    );
    discountBillPerItem = getDiscountBillPerItem(item, items, suppDiscount, sum_sub_total);

    subTotalTaxPerItem = (subtotalPerItem - discountBillPerItem) * tax_pct;
  } else {
    subtotalPerItem = getSubTotalPerItem(
      item.item_price,
      item.return_qty,
      discount_item
    );
    discountBillPerItem = getDiscountBillPerItem(item, items, suppDiscount, sum_sub_total);
    subTotalTaxPerItem = (subtotalPerItem - discountBillPerItem) * tax_pct;
  }

  return parseFloat(subTotalTaxPerItem);
};

/**
 * @param {object} item item yang akan dihitung subtotal ppn & diskonnya
 * @param {array} items list items
 * @param {string} suppDiscount discount supplier
 * */
const getSubTotalTaxPerItemInclusive = (
  item,
  items,
  suppDiscount,
  sum_sub_total
) => {
  const tax_pct = parseFloat(item.tax_pct);
  let subTotalTaxPerItem = "0.00";
  let subtotalPerItem = 0;
  let discountBillPerItem = 0;
  let discount_item = getReturnDiscount(item);
  // const price =
  //   (parseFloat(item.item_price) / parseFloat(item.pi_unit_conv)) *
  //   parseFloat(item.unit_conv) *
  //   item.return_qty;
  if (tax_pct === 0) {
    return parseFloat(subTotalTaxPerItem);
  } else if (tax_pct > 0) {
    subtotalPerItem = getSubTotalPerItem(
      item.item_price,
      item.return_qty,
      discount_item
    );
    discountBillPerItem = getDiscountBillPerItem(item, items, suppDiscount, sum_sub_total);

    subTotalTaxPerItem =
      ((subtotalPerItem - discountBillPerItem) * tax_pct * 100) /
      (100 + 100 * tax_pct);
  }
  return parseFloat(subTotalTaxPerItem);
};

/**
 * @param {object} item item yang akan dihitung discount bill nya
 * @param {array} items list item
 * @param {string} suppDiscount discount supplier
 * */
export const getDiscountBillPerItem = (item, items, suppDiscount, sum_sub_total) => {
  // const tax_pct = parseFloat(item.tax_pct);
  let discountBillPerItem = 0;
  // if (tax_pct === 0) {
  //   return parseFloat("0.00");
  // } else {
  const sumSubtotal = sum_sub_total;
  const discountBill = getDiscount(suppDiscount, sumSubtotal);
  const discount_item = getReturnDiscount(item);
  // console.log(sumSubtotal, discountBill, discount_item);
  // const price =
  //   (parseFloat(item.item_price) / parseFloat(item.pi_unit_conv)) *
  //   parseFloat(item.unit_conv) *
  //   item.return_qty;
  discountBillPerItem =
    (getSubTotalPerItem(item.item_price, item.return_qty, discount_item) /
      sumSubtotal) *
    discountBill;
  // }

  return parseFloat(discountBillPerItem);
};

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { items } = props;

    this.state = {
      dataSource: [...items],
      count: items.length + 1,
      total_return: 0,
      total_return_amount: 0,
      visible: false
    };
  }

  componentDidUpdate(prevProps) {
    const {
      loadingTransactions,
      items,
      purchase_invoice,
      currency_rate,
      discount,
      inclusive_tax,
      handleSaveItemList,
      clearItems,
      clear
    } = this.props;

    if (!loadingTransactions && items.length !== 0) {
      let newData = [];
      let newTotRetQty = 0;
      let newTotRetAmount = 0;
      let newTotDiscount = 0;
      let newTotTax = 0;
      let newIndex = 0;
      const sum_sub_total = purchase_invoice ? purchase_invoice.sum_sub_total : 0;

      items.map(item => {
        let init = {};

        init.index_no = item.index_no;
        init.id_transaction_detail = item.id_transaction_detail
          ? item.id_transaction_detail
          : item.id_purchase_invoice_detail;
        init.item_code = item.item_code;
        init.item_name = item.item_name;
        init.qty = parseFloat(item.qty);
        init.discount = item.discount ? item.discount : item.pi_detail.discount;
        init.item_price = parseFloat(item.item_price);
        init.returned_qty = item.returned_qty
          ? parseFloat(item.returned_qty)
          : item.pi_detail
          ? parseFloat(item.pi_detail.returned_qty)
          : 0;
        init.return_qty = item.return_qty ? parseFloat(item.return_qty) : init.qty - init.returned_qty;

        init.id_tax = item.id_tax;
        init.tax_pct =
          item.tax_pct || item.tax_pct === 0
            ? parseFloat(item.tax_pct)
            : parseFloat(item.tax.percentage);
        init.tax_name = item.tax ? item.tax.tax_name : item.tax_name;

        init.unit_conversion = item.unit_conversion
          ? item.unit_conversion
          : item.item.unit_conversion;
        init.id_return_unit = item.id_return_unit
          ? item.id_return_unit
          : item.id_unit;
        init.return_unit_code = item.unit
          ? item.unit.unit_code
          : item.return_unit_code;
        init.unit_conv = getUnitConversion(
          init.id_return_unit,
          init.unit_conversion
        );
        // untuk filter unit conversion yang dibawah PI unit
        init.pi_unit_conv = init.unit_conv;

        init.id_base_unit = item.id_base_unit;
        init.base_unit_code = item.base_unit
          ? item.base_unit.unit_code
          : item.base_unit_code;
        init.base_qty = init.return_qty * init.unit_conv;

        // untuk perhitungan discount
        init.pi_base_qty = init.qty * init.pi_unit_conv;

        init.cost_per_unit = parseFloat(item.cost_per_unit);

        // cost per unit dalam currency
        init.cost_per_unit_curr =
          init.cost_per_unit / parseFloat(currency_rate);

        init.return_sub_total =
          init.cost_per_unit_curr * init.base_qty;

        init.sub_total = item.sub_total
          ? parseFloat(item.sub_total)
          : parseFloat(item.purchase_sub_total);

        newData.push(init);
      });
      // const sum_sub_total = getSumSubTotal(items);
      newData.map(currData => {
        // discount bill + discount di PT
        currData.discount_item =
          getDiscountBillPerItem(currData, newData, discount, sum_sub_total) +
          getReturnDiscountAmount(currData);
      });

      newData.map(currData => {
        currData.sub_total_tax = inclusive_tax
          ? getSubTotalTaxPerItemInclusive(currData, newData, discount, sum_sub_total)
          : getSubTotalTaxPerItem(currData, newData, discount, sum_sub_total);

        newTotTax += currData.sub_total_tax;
        newTotRetQty += currData.base_qty;
        newTotRetAmount += currData.return_sub_total;
        newTotDiscount += currData.discount_item;
        newIndex = parseInt(currData.index_no) + 1;
      });
      // const totalTax = getTotalTax([...items], discount, inclusive_tax);
      newTotRetAmount = newTotRetAmount - newTotDiscount + newTotTax;

      this.setState({
        dataSource: [...newData],
        count: newIndex,
        total_return: newTotRetQty,
        total_return_amount: newTotRetAmount
      });

      clearItems(
        newData,
        newTotDiscount,
        newTotTax,
        newTotRetQty,
        newTotRetAmount
      );
    }
    // clear item jika location/supplier berubah
    else if (clear) {
      this.setState({ dataSource: [], count: 1 });
      handleSaveItemList([], 0, 0, 0, 0);
    }
    // perubahan currency rate
    else if (prevProps.currency_rate !== currency_rate && currency_rate) {
      const { purchase_invoice } = this.props;
      let newData = this.state.dataSource;
      let newTotRetQty = 0;
      let newTotRetAmount = 0;
      let newTotDiscount = 0;
      let newTotTax = 0;
      const sum_sub_total = purchase_invoice ? purchase_invoice.sum_sub_total : 0;

      newData.map(data => {
        data.cost_per_unit_curr =
          data.cost_per_unit / parseFloat(currency_rate);
        data.return_sub_total =
          parseFloat(data.cost_per_unit_curr) * parseFloat(data.base_qty);
      });

      newData.map(data => {
        data.discount_item =
          getDiscountBillPerItem(data, newData, discount, sum_sub_total) +
          getReturnDiscountAmount(data);

        data.sub_total_tax = inclusive_tax
          ? getSubTotalTaxPerItemInclusive(data, newData, discount, sum_sub_total)
          : getSubTotalTaxPerItem(data, newData, discount, sum_sub_total);

        newTotRetQty += parseFloat(data.base_qty);
        newTotRetAmount += parseFloat(data.return_sub_total);
        newTotDiscount += parseFloat(data.discount_item);
        newTotTax += parseFloat(data.sub_total_tax);
      });

      this.setState({
        dataSource: [...newData],
        total_return: newTotRetQty,
        total_return_amount: newTotRetAmount
      });

      handleSaveItemList(
        newData,
        newTotDiscount,
        newTotTax,
        newTotRetQty,
        newTotRetAmount
      );
    }
  }

  handleDelete = index_no => {
    const { handleSaveItemList } = this.props;
    let newTotRetQty = 0;
    let newTotRetAmount = 0;
    let newTotDiscount = 0;
    let newTotTax = 0;

    const dataSource = this.state.dataSource;

    let newData = dataSource.filter(currData => currData.index_no !== index_no);
    newData.map((currData, index) => {
      currData.index_no = index + 1;
      newTotRetQty += parseFloat(currData.base_qty);
      newTotRetAmount += parseFloat(currData.return_sub_total);
      newTotDiscount += parseFloat(currData.discount_item);
      newTotTax += parseFloat(currData.sub_total_tax);
    });

    // const totalTax = getTotalTax([...newData], discount, inclusive_tax);
    newTotRetAmount = newTotRetAmount - newTotDiscount + newTotTax;

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1,
      total_return: newTotRetQty,
      total_return_amount: newTotRetAmount
    });

    handleSaveItemList(
      newData,
      newTotDiscount,
      newTotTax,
      newTotRetQty,
      newTotRetAmount
    );
  };

  handleSave = row => {
    const { handleSaveItemList, purchase_invoice, discount, inclusive_tax } = this.props;
    const newData = this.state.dataSource;
    let newTotRetQty = 0;
    let newTotRetAmount = 0;
    let newTotDiscount = 0;
    let newTotTax = 0;
    const sum_sub_total = purchase_invoice ? purchase_invoice.sum_sub_total : 0;

    const index = newData.findIndex(
      currData => row.index_no === currData.index_no
    );

    row.base_qty = parseFloat(row.return_qty) * parseFloat(row.unit_conv);
    // row.return_sub_total =
    //   parseFloat(row.netto_last_purchase) * parseFloat(row.base_qty);
    row.return_sub_total =
      parseFloat(row.cost_per_unit_curr) * parseFloat(row.base_qty);

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    // const sum_sub_total = getSumSubTotal(newData);
    // row.unit_conv = getUnitConversion(row.id_return_unit, row.unit_conversion);
    // row.base_qty = parseFloat(row.return_qty) * parseFloat(row.unit_conv);
    row.discount_item =
      getDiscountBillPerItem(row, newData, discount, sum_sub_total) +
      getReturnDiscountAmount(row);

    row.sub_total_tax = inclusive_tax
      ? getSubTotalTaxPerItemInclusive(row, newData, discount, sum_sub_total)
      : getSubTotalTaxPerItem(row, newData, discount, sum_sub_total);

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    newData.map(currData => {
      newTotRetQty += parseFloat(currData.base_qty);
      newTotRetAmount += parseFloat(currData.return_sub_total);
      newTotDiscount += parseFloat(currData.discount_item);
      newTotTax += parseFloat(currData.sub_total_tax);
    });

    // console.log(row);
    // const totalTax = getTotalTax([...newData, discount, inclusive_tax]);
    newTotRetAmount = newTotRetAmount - newTotDiscount + newTotTax;

    this.setState({
      dataSource: [...newData],
      total_return: newTotRetQty,
      total_return_amount: newTotRetAmount
    });

    handleSaveItemList(
      newData,
      newTotDiscount,
      newTotTax,
      newTotRetQty,
      newTotRetAmount
    );
  };

  render() {
    // console.log(this.props.purchaseOrder);

    const { dataSource } = this.state;
    const { discount, purchase_invoice, inclusive_tax } = this.props;
    const sum_sub_total = purchase_invoice ? purchase_invoice.sum_sub_total : 0;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        width: "5%",
        align: "right",
        render: text => {
          if (isNaN(text)) {
            return {
              children: text,
              props: {
                colSpan: 7
              }
            };
          } else {
            return {
              children: text,
              props: {
                colSpan: 1
              }
            };
          }
        }
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code",
        width: "25%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          if (text && text.length > 10) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 10) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        width: "30%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="PIQty" />}>
            <a href="javascript:;"><IntlMessages id="PIQtys" /></a>
          </Tooltip>
        ),
        dataIndex: "qty", // dari PR
        width: "15%",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          return (<><NumberFormat value={text} /> {row.return_unit_code}</>);
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="purchaseInvoiceBaseQuantity" />}>
            <a href="javascript:;"><IntlMessages id="piBaseQty" /></a>
          </Tooltip>
        ),
        dataIndex: "pi_base_qty", // dari PR
        width: "15%",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          return (<><NumberFormat value={parseFloat(row.qty) * parseFloat(row.unit_conv)} /> {row.base_unit_code}</>);
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="costPerUnit" />}>
            <a href="javascript:;"><IntlMessages id="cost" /></a>
          </Tooltip>
        ),
        dataIndex: "cost_per_unit_curr",
        width: "15%",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return (<NumberFormat value={text} />);
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="returnedQty" />}>
            <a href="javascript:;"><IntlMessages id="rtrndQty" /></a>
          </Tooltip>
        ),
        dataIndex: "returned_qty", // dari PR
        width: "15%",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return (<><NumberFormat value={text} /> {row.return_unit_code}</>);
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="returnQuantity" />}>
            <a href="javascript:;"><IntlMessages id="returnQty" /></a>
          </Tooltip>
        ),
        dataIndex: "return_qty", // dari PR
        width: "15%",
        align: "right",
        editable: true,
        onCell: record => {
          if (isNaN(record.index_no)) {
            return {
              record,
              title: (
                <Tooltip placement="top" title={<IntlMessages id="returnQuantity" />}>
                  <a href="javascript:;"><IntlMessages id="returnQty" /></a>
                </Tooltip>
              ),
              dataIndex: "return_qty", // dari PR
              // width: "10%",
              status: this.props.status
            };
          } else {
            return {
              record,
              title: (
                <Tooltip placement="top" title={<IntlMessages id="returnQuantity" />}>
                  <a href="javascript:;"><IntlMessages id="returnQty" /></a>
                </Tooltip>
              ),
              dataIndex: "return_qty", // dari PR
              width: "10%",
              editable: true,
              handleSave: this.handleSave,
              status: this.props.status
            };
          }
        }
      },
      {
        title: getLanguage({ id: "returnUnit" }),
        dataIndex: "return_unit_code",
        align: "center",
        width: "10%"
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="returnBaseQuantity" />}>
            <a href="javascript:;"><IntlMessages id="returnBaseQty" /></a>
          </Tooltip>
        ),
        dataIndex: "base_qty",
        width: "15%",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 3
              }
            };
          }

          return (<><NumberFormat value={text} /> {row.base_unit_code}</>);
        }
      },
      {
        title: getLanguage({ id: "returnSubTotal" }),
        dataIndex: "return_sub_total",
        width: "15%",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return (<NumberFormat value={text} /> );
        }
      },
    ];

    let operation = 
      {
        title: "",
        dataIndex: "operation",
        width: "15%",
        align: "center",
        fixed: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          else {
            return (
              <Popconfirm
                title={<IntlMessages id="confirmDelete" />}
                onConfirm={() => this.handleDelete(row.index_no)}
                okText={<IntlMessages id="yes" />}
                cancelText={<IntlMessages id="no" />}
              >
                <Tooltip
                  title={<IntlMessages id="delete" />}
                >
                  <Button
                    //style={{ borderRadius: "50%" }}
                    className="invoiceDltBtn"
                    size="small"
                  >
                    <i className="ion-trash-b" />
                  </Button>
                </Tooltip>
              </Popconfirm>
            );
          }
        }
      }

    if ((!this.props.status || this.props.status === 1) && columns.length === 11){
      columns.push(operation)
    }

    const total = getTotal(dataSource, discount);
    const total_tax = getTotalTax(dataSource, discount, inclusive_tax, sum_sub_total);
    const total_return_amount = parseFloat(total.totalReturnAmount) + total_tax;

    const info = [
      {
        index_no: `${getLanguage({ id: "purchaseReturns.totalReturnQuantity" })} (${getLanguage({ id: "bsUnt" })})`,
        return_qty: <NumberFormat value={total.totalReturnQty} />
      },
      {
        index_no: getLanguage({ id: "purchaseReturns.totalDiscount" }),
        return_qty: <NumberFormat value={total.totalDiscount} />
      },
      {
        index_no: getLanguage({ id: "totalTax" }),
        return_qty: <NumberFormat value={total_tax} />
      },
      {
        index_no: getLanguage({ id: "total" }),
        return_qty: <NumberFormat value={total_return_amount} />
      }
    ];
    const modifiedData = dataSource.length > 0 ? [...dataSource, ...info] : [];

    return (
      <CardWrapper>
        <Scrollbars style={{ width: "100%" }}>
          <TableWrapper
            className="invoiceListTable"
            components={components}
            rowKey={record => record.index_no}
            rowClassName={() => "editable-row"}
            dataSource={modifiedData}
            columns={columns}
            pagination={false}
            // scroll={{ x: 1300 }}
          />
        </Scrollbars>
      </CardWrapper>
    );
  }
}

export default EditableTable;
