import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import PurchaseReturnUpdateForm from "./updateForm";
import PurchaseReturnDocUpdateForm from "./updateDocForm";
import HistoryPurchaseReturn from "./history";

import notification from "../../../../components/notification";
import { Spin, Tooltip, Tabs } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/purchase/purchaseReturn/actions";
import { Box } from "../../../App/app.style";
import Print from "./print";

const redirecTo = "/dashboard/purchases/purchase-returns";
const TabPane = Tabs.TabPane;

class UpdatePurchaseReturn extends Component {
  state = {
    visible: false
  };

  componentDidMount() {
    const { editPurchaseReturn } = this.props;
    const { id } = this.props.match.params;
    editPurchaseReturn(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.purchaseReturn.error) {
      notification("error", this.props.purchaseReturn.error);
      this.props.clearError();
    }
    if (this.props.purchaseReturn.success) {
      // this.props.history.replace(
      //   redirecTo + "/update/" + this.props.purchaseReturn.id_purchase_invoice
      // );
      //   this.props.history.replace(redirecTo);
      notification("success", this.props.purchaseReturn.success);
      this.props.clearSuccess();
    }
    // if (this.props.purchaseReturn.warning) {
    //   // this.props.history.replace(
    //   //   redirecTo + "/update/" + this.props.purchaseReturn.id_purchase_invoice
    //   // );
    //   //   this.props.history.replace(redirecTo);
    //   notification("warning", this.props.purchaseReturn.warning);
    //   this.props.clearSuccess();
    // }
  }

  handleModalHistory = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSubmit = value => {
    const { updatePurchaseReturn } = this.props;
    const { id } = this.props.match.params;
    updatePurchaseReturn(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelPurchaseReturn } = this.props;
    const { id } = this.props.match.params;
    cancelPurchaseReturn(id, value);
  };

  render() {
    // console.log(this.props);
    const {
      loading,
      loadingSubmit,
      purchaseReturn,
      printData
    } = this.props.purchaseReturn;
    const modified = { ...purchaseReturn, ...printData };

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="purchaseReturns.update.title" />}
            marginBottom={true}
          >
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={modified} />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirecTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <HistoryPurchaseReturn
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={purchaseReturn ? purchaseReturn.description : null}
          />

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="jobCostings.tab.detail" />} key="1">
              {loading && !loadingSubmit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <PurchaseReturnUpdateForm
                  submit={this.handleSubmit}
                  purchaseReturn={this.props.purchaseReturn}
                  handleSubmitCancel={this.handleSubmitCancel}
                />
              )}
            </TabPane>
            <TabPane
              tab={<IntlMessages id="jobCostings.tab.attachment" />}
              key="2"
            >
              {loadingSubmit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <PurchaseReturnDocUpdateForm
                  submit={this.handleSubmit}
                  purchaseReturn={this.props.purchaseReturn}
                  params={this.props.match.params}
                />
              )}
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    purchaseReturn: state.purchaseReturn,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(UpdatePurchaseReturn);
